import uCharts from '../../js_sdk/u-charts/u-charts/u-charts.js';
var _self;
var canvaLineA = null;

const query = require('../../static/utils/utils').Query;
const login = require('../../static/utils/utils').Login;
const Login = new login();
const Query = new query();
//引入api
import {
	getLineData,
	sprintQuery,
	departmentQueryAll
} from '../../static/utils/api.js';

var _this;
export default {
	data() {
		return {
			departmentArray: [],
			departmentIndex: 0,
			sprintArray: [],
			sprintIndex: 0,
			windowWidth: 320,
			cWidth: '',
			cHeight: '',
			pixelRatio: 1,
			userInfo: "", //用户的信息
			projectId: "", //项目的id
			sprintId: "", //冲刺的id
			roleId: "", //权限的id
			departmentId: "" //专业id 当用户的权限为2的时候默认进入自己的专业
		}
	},
	onLoad() {
		_self = this;
		this.cWidth = uni.upx2px(400);
		this.cHeight = uni.upx2px(500);
	},
	onShow() {

		let _this = this

		uni.getSystemInfo({
			success: function(res) {
				_this.windowWidth = res.windowWidth;

			}
		});
		uni.request({
			url: `http://120.79.49.245:7377/picture/getLineData?sprintId=14&projectId=1&userId=27`,
			method: "POST",
			header: {
				'ContentType': 'application/x-www-form-urlencoded' //自定义请求头信息
			},
			success: (res) => {
				if (res.data.code === 200) {
					let categories = res.data.data.categories;
					let plainData = res.data.data.plainData;
					let planDataList = []
					for (var i = 0; i < plainData.length; i++) {
						planDataList[i] = plainData[i].workTime;
					}
					let trueData = res.data.data.trueData;
					let trueDataList = []
					for (var i = 0; i < trueData.length; i++) {
						trueDataList[i] = trueData[i].workTime;
					}
					let LineA = {
						categories: categories,
						series: [{
							color: "#1890ff",
							data: planDataList,
							name: "计划数据",
							type: "line",
							format: (val) => {
								return val.toFixed(2)
							}
						}, {
							color: "#2fc25b",
							data: trueDataList,
							name: "实际数据",
							type: "line",
							format: (val) => {
								return val.toFixed(2)
							}
						}, ]
					};
					console.log(LineA)

					_this.showLineA("canvasLineA", LineA);
				}
			}
		});
		_this = this;
		uni.getStorage({
			key: 'userInfo',
			success: (res) => {
				let id = {
					id: res.data.id
				}
				Query.findUser(id)
					.then(data => {
						console.log("返回的用户信息", data.data.records[0])
						_this.userInfo = data.data.records[0];
						uni.getStorage({
							key: "nowInProject",
							success: (res) => {
								_this.roleId = res.data.roleId;
								_this.projectId = res.data.projectId;
								uni.getStorage({
									key: 'sprintId',
									success: (res) => {
										_this.sprintId = res.data;
										//获取所有专业
										this.getDepartmentArray(res.data);
										this.getSprintArray(res.data);
									}
								})
							},
						})
					})
					.catch(Error => {
						uni.showToast({
							title: "网络错误",
							icon: "none",
							duration: 1000
						})
					})
			},
			fail: () => {
				uni.redirectTo({
					url: "../login/login"
				})
			}
		})


	},

	methods: {
		getDepartmentArray(projectId) {
			let _this = this
			uni.request({
				url: `${departmentQueryAll}?projectId=${projectId}`,
				method: "POST",
				data: {},
				header: {
					'ContentType': 'application/json'
				},
				success: (res) => {
					if (res.data.code === 200) {
						let departmentArray = []
						for (var i = 0; i < res.data.data.length; i++) {
							departmentArray[i] = res.data.data[i].name
						}
						_this.departmentArray = departmentArray
						console.log(departmentArray)
					}
				}
			});
		},
		getSprintArray(projectId) {
			let _this = this
			uni.request({
				url: `${sprintQuery}`,
				method: "POST",
				data: {
					"pageNum": 0,
					"pageSize": 1000,
					"projectId": projectId
				},
				header: {
					'ContentType': 'application/json'
				},
				success: (res) => {
					if (res.data.code === 200) {
						let sprintArray = []
						for (var i = 0; i < res.data.data.length; i++) {
							sprintArray[i] = res.data.data[i].sprintName
						}
						_this.sprintArray = sprintArray
					}
				}
			});
		},
		departmentPickerChange: function(e) {
			this.departmentIndex = e.target.value
			console.log('获取值', this.departmentArray[this.departmentIndex])

		},
		sprintPickerChange: function(e) {
			this.sprintIndex = e.target.value
			console.log('获取值', this.sprintArray[this.sprintIndex])

		},
		scroll: function(e) {
			var scrollLeft = e.detail.scrollLeft;
			this.setData({
				scrollLeft: scrollLeft
			})
			console.log(scrollLeft)
		},
		showLineA(canvasId, chartData) {
			let _this = this
			canvaLineA = new uCharts({
				$this: _this,
				canvasId: canvasId,
				type: 'line',
				enableScroll: false, //可拖动
				fontSize: 11,
				legend: true,
				dataLabel: false,
				dataPointShape: true,
				background: '#21242E',
				pixelRatio: _this.pixelRatio,
				categories: chartData.categories,
				series: chartData.series,
				animation: true,
				xAxis: {
					type: 'grid',
					disableGrid: true,
					dashLength: 8,
					scrollShow: true,
					fontColor: '#058',
				},
				yAxis: {
					title: '预估剩余时间（小时）',
					titleFontColor: 'white',
					gridType: 'solid',
					gridColor: '#CCCCCC',
					splitNumber: 8, //y轴网格的数量
					fontColor: '#058',
					format: (val) => {
						return val.toFixed(0)
					}
				},
				width: _this.windowWidth,
				height: _this.cHeight * _this.pixelRatio,
				extra: {
					line: {
						type: 'straight'
					}
				}
			});

		},
		touchLineA(e) {
			canvaLineA.showToolTip(e, {
				format: function(item, category) {
					return category + ' ' + item.name + ':' + item.data
				}
			});
		}
	}
}
